import React, { Component } from 'react'
import { Flex, Alert, Grid, GridItem, Stack, Container, Button, ButtonGroup, Text, Box } from '@chakra-ui/react'
import { connect } from 'react-redux'
import styled from '@emotion/styled'
import { THE_LAST_EPISODE, NEXT_EPISODE, EPISODE_CURRENT } from '../../store/actionTypes'

class PlayList extends Component {
  constructor(props) {
    super(props)
  }
  render() {  
    const { lists, id, nextEpisode, lastEpisode, url, episode, subTitle, setEpisodeCurrent} = this.props
    return (
      <Box  position="sticky"
        top="0"
        bgColor="primary.100"
        zIndex="sticky"
        alignItems="center"
        flexWrap="nowrap"
        h="100%"
        overflowX="auto"
        px="2"
        css={{
          WebkitOverflowScrolling: "touch",
          msOverflowStyle: "-ms-autohiding-scrollbar"
        }}>


    <Stack spacing={3}>
      <Alert status='success' variant='solid'>
        <ButtonGroup colorScheme="teal" m={2} size='xs' isAttached variant='outline'>
          <Button mr='-px'>1-30</Button>
        </ButtonGroup>
        <ButtonGroup colorScheme="teal" m={2} size='xs' isAttached variant='outline'>
          <Button mr='-px'>31-60</Button>
        </ButtonGroup>
        <ButtonGroup m={2} size='xs' isAttached variant='outline'>
          <Button mr='-px'>61-90</Button>
        </ButtonGroup>
      </Alert>
    </Stack>
          {

            lists.map((item, index) => {
              return (
                <Button colorScheme="teal" m="1" p={0} width="36px" onClick={() => setEpisodeCurrent(index)} key={index} size="sm">{index+1}</Button>
              )
            })

          }
      </Box>
    )
    
  }
}

const mapStateToProps = (state) => {

  const { id, url, episode, subTitle, lists } = state.playerParams
  console.log(state.playerParams)
  return {
    id,
    url,
    episode,
    subTitle,
    lists
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    setEpisodeCurrent(id) {
      const action = {
        type: EPISODE_CURRENT,
        id
      }
      dispatch(action)
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(PlayList)
